import React, { createContext, useContext, useState } from 'react';

const sortByContext = createContext();

const SortByProvider = ({ children }) => {
  const [currentSortBy, setCurrentSortBy] = useState('publishedAt');
  const updateSort = newSortBy => {
    setCurrentSortBy(newSortBy);
  };

  return (
    <sortByContext.Provider value={{ currentSortBy, updateSort }}>
      {children}
    </sortByContext.Provider>
  );
};

export default SortByProvider;

export const useSortBy = () => useContext(sortByContext);
