import React, { useEffect, useState } from 'react';
import CardGrid from '../components/CardGrid';
import MainPageLayout from './MainPageLayout';
import { getApiResponse } from '../helper/fetchApi';
import {
  useCategory,
  useCountry,
} from '../context/TopHeadlinesContextProvider';
import { countries } from '../misc/countryCodes';

const HomePage = () => {
  const { currentCategory } = useCategory();
  const { currentCountryIndex } = useCountry();
  const [apiResult, setApiResult] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const setLoadState = async () => {
      setIsLoading(true);
      await getApiResponse({
        type: 'top-headlines',
        country: `country=${countries[currentCountryIndex].countryCode}`,
        category: `category=${currentCategory}`,
      }).then(result => {
        setApiResult(result.articles);
      });
      setIsLoading(false);
    };

    setLoadState();
  }, [currentCategory, currentCountryIndex]);

  /* eslint-disable */
  return (
    <MainPageLayout sortFeature={false} selectCountry>
      <div className="home">
        <div className="ticker">
          <div>Top Headlines</div>
          <marquee>
            <ul>
              {apiResult.map((article, index) => {
                return <li key={index}>{article.title}</li>;
              })}
            </ul>
          </marquee>
        </div>
        <CardGrid apiResult={apiResult} isLoading={isLoading} />
      </div>
    </MainPageLayout>
  );
};

export default HomePage;
