const baseApi = 'https://newsapi.org/v2/';
const apiKey = '9a6833ac364443179534d68a46b15bd4';

const getApiEverything = (query, from, to, page, sortBy) => {
  return fetch(
    `${baseApi}everything?${query}&${from}&${to}&${page}&${sortBy}&language=en&pageSize=20&apiKey=${apiKey}`
  ).then(response => response.json());
};

const getApiTopHeadlines = (category, country, page) => {
  return fetch(
    `${baseApi}top-headlines?${category}&${country}&${page}&pageSize=100&apiKey=${apiKey}`
  ).then(response => response.json());
};

export const getApiResponse = ({
  type,
  query = '',
  from = '',
  to = '',
  sortBy = '',
  page = 'page=1',
  category = 'category=general',
  country = '',
}) => {
  if (type === 'top-headlines') {
    return getApiTopHeadlines(category, country, page);
  }
  return getApiEverything(query, from, to, page, sortBy);
};
