import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { StyledLink } from '../styledComponents/StyledLink';
import { useQuery } from '../context/TopHeadlinesContextProvider';
import { useSortBy } from '../context/SortByProvider';

const SearchLink = styled(StyledLink)`
  background-color: black;
  border-radius: 0 0.4rem 0.4rem 0;
`;

const SearchBar = () => {
  const { currentQuery, updateQuery } = useQuery();
  const { updateSort } = useSortBy();

  const inputRef = useRef();

  useEffect(() => {
    inputRef.current.value = currentQuery;
  });
  return (
    <div className="input-container">
      <input type="text" ref={inputRef} />
      <SearchLink
        to="/search"
        onClick={() => {
          updateQuery(inputRef.current.value);
          updateSort('publishedAt');
        }}
      >
        <div className="search-icon">
          <i className="fas fa-search" />
        </div>
      </SearchLink>
    </div>
  );
};

export default SearchBar;
