import React from 'react';
import { Switch, Route } from 'react-router-dom';
import TopHeadlinesContextProvider from './context/TopHeadlinesContextProvider';
import HomePage from './pages/HomePage';
import SearchResults from './pages/SearchResults';
import SortByProvider from './context/SortByProvider';

const App = () => {
  return (
    <Switch>
      <TopHeadlinesContextProvider>
        <SortByProvider>
          <Route exact path="/">
            <HomePage />
          </Route>
          <Route exact path="/search">
            <SearchResults />
          </Route>
        </SortByProvider>
      </TopHeadlinesContextProvider>

      <Route>Page Does Not Exists</Route>
    </Switch>
  );
};

export default App;
