import React from 'react';
import styled from 'styled-components';
import logo from '../images/logo.jpg';

const StyledDiv = styled.div`
  height: 2rem;

  img {
    height: 100%;
  }
`;

const Logo = () => {
  return (
    <StyledDiv>
      <img src={logo} alt="Logo" />
    </StyledDiv>
  );
};

export default Logo;
