import React, { useEffect, useState } from 'react';
import CardGrid from '../components/CardGrid';
import MainPageLayout from './MainPageLayout';
import { useQuery } from '../context/TopHeadlinesContextProvider';
import { useSortBy } from '../context/SortByProvider';
import { getApiResponse } from '../helper/fetchApi';

const SearchResults = () => {
  const { currentQuery } = useQuery();
  const { currentSortBy } = useSortBy();
  const [apiResult, setApiResult] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const setLoadState = async () => {
      setIsLoading(true);
      await getApiResponse({
        type: 'everything',
        query: `qInTitle=${currentQuery}`,
        sortBy: `sortBy=${currentSortBy}`,
      }).then(result => {
        setApiResult(result.articles);
      });
      setIsLoading(false);
    };

    setLoadState();
  }, [currentQuery, currentSortBy]);

  return (
    <MainPageLayout sortFeature selectCountry={false}>
      <CardGrid apiResult={apiResult} isLoading={isLoading} />
    </MainPageLayout>
  );
};

export default SearchResults;
